@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,200;0,400;0,500;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@200;300;400;500;600;700&display=swap');
@import "keyframes";

// ===================media=====================
$md:  1200px;
$mds: 800px;
$sm:  768px;
$xs:  500px;

// ===================grid=====================
$min_width: 320px;
$tablet: 768px;


// ===================colors=====================

$black:         #121212;
$white:			#fff;
$blue:          #01DAC7;
$blue-hover:    #6ED7DD;
$blue-active:   #CAFFFD;
$grey:          #BABABA;
$yellow:        #FF9F0A;


$anim: .3s ease;

$ff:        'IBM Plex Mono', monospace;
$ff-text:   'Mulish', sans-serif;

// ========================mixin==============================

@function width($col, $margin:0px) {
  @return calc((100% / #{$col}) - #{$margin});
}

// ===================fonts=====================


@mixin ie {
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    @content;
  }
}

// ========================mixin==============================
// how use
// @include fluid-type($min_width, $max_width, 12px, 16px);
// /how use

@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

@mixin fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
  $u1: unit($min-vw);
  $u2: unit($max-vw);
  $u3: unit($min-font-size);
  $u4: unit($max-font-size);

  @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
    & {
      font-size: $min-font-size;
      @media screen and (min-width: $min-vw) {
        font-size: calc(#{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} * ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)}));
      }
      @media screen and (min-width: $max-vw) {
        font-size: $max-font-size;
      }
    }
  }
}

@mixin text-limit($row) {
  display: -webkit-box;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $row;
}

@mixin triangle($width, $color) {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: $width $width 0 $width;
  border-color: $color transparent transparent transparent;
}

@mixin fixInputIOS {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

@mixin tableColWidth($sizes...) {
  @for $i from 0 to length($sizes) {
    &:nth-child(#{$i + 1}) {
      width: #{nth($sizes, $i + 1)}
    }
  }
}


@mixin center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin img($fit: cover) {
  @include center;
  flex-shrink: 0;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    object-fit: $fit;
  }
}
@mixin img-full {
  @include center;
  flex-shrink: 0;
  img,
  picture{
    max-width: 100%;
    max-height: 100%;
  }
}



@mixin h1{
  @include fluid-type(1250px, 1520px, 46px, 58px);
  // font-weight: 600;
  text-transform: uppercase;
  line-height: 120%;
  font-family: $ff;
  letter-spacing: -0.03em;
  @include w($md){
    @include fluid-type(320px, 400px, 32px, 41px);
  }
}
@mixin h2{
  //@include fluid-type(1250px, 1366px, 30px, 44px);
  @include fluid-type(1250px, 1520px, 46px, 58px);
  // font-weight: 600;
  text-transform: uppercase;
  line-height: 120%;
  font-family: $ff;
  letter-spacing: -0.03em;
  @include w($md){
    @include fluid-type(320px, 400px, 32px, 41px);
  }
}
@mixin h3{
  font-weight: 700;
  line-height: 170%;
  @include fluid-type(1366px, 1500px, 20px, 26px);
  @include w($md){
    //@include fluid-type(350px, 800px, 34px, 40px);
  }
}
@mixin h4{
  @include fluid-type(1366px, 1500px, 18px, 22px);
  // font-weight: 600;
  line-height: 164%;
  @include w($md){
    @include fluid-type(320px, 420px, 16px, 18px);
    line-height: 150%;
  }
}


@mixin txt($weight:400){
  font-weight: $weight;
  @include fluid-type(1200px, 1366px, 14px, 18px);
  line-height: 1.7;
  color: $grey;
  @include w($md){
    @include fluid-type(320px, 400px, 14px, 17px);
  }
}

@mixin txt-big($weight:600){
  font-weight: $weight;
  @include fluid-type(1200px, 1366px, 16px, 20px);
  line-height: 1.7;
  font-family: $ff-text;
  color: $grey;
  @include w($md){
    @include fluid-type(320px, 400px, 15px, 18px);
    line-height: 160%;
  }
}




@mixin w($size, $sizeMin:0px){
  @if ($sizeMin == 0){
    @media (max-width: ($size - 0.02)) { @content; }
  } @else {
    @media (min-width: $sizeMin) and (max-width: ($size - 0.02)){ @content; }
  }
}

@mixin h($size){
  @media (max-height: $size) { @content; }
}

@mixin offHoverOnDevices {
  @media screen and (min-width: $md) {  @content;  }
}

@mixin minw($size) {
  @media (min-width: $size) { @content; }
}
