.swiper-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.swiper-button-next, .swiper-button-prev {
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  outline: none;
}
.swiper-button-next::before, .swiper-button-next::after, .swiper-button-prev::before, .swiper-button-prev::after {
  font-family: swiper-icons;
  text-transform: none !important;
  letter-spacing: 0;
  text-transform: none;
  font-variant: initial;
  line-height: 1;
  font-size: 18px;
  font-weight: 700;
}
.swiper-button-next::before, .swiper-button-prev::before {
  color: #fff;
  position: absolute;
  top: calc(50% - 9px);
  font-weight: 400;
  z-index: 1;
}
.swiper-button-next::after, .swiper-button-prev::after {
  color: #121212;
  position: relative;
  z-index: 2;
  transition: 0.3s ease;
}
.swiper-button-next:focus, .swiper-button-prev:focus {
  outline: none;
}
.swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
  opacity: 0;
}
@media (min-width: 0px) and (max-width: 767.98px) {
  .swiper-button-next, .swiper-button-prev {
    display: none;
  }
}
.swiper-button-prev::before {
  content: "prev";
  left: 9px;
}
.swiper-button-next::before {
  content: "next";
  right: 9px;
}
.swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal {
  left: 50%;
  width: auto;
}
.swiper-container-horizontal > .swiper-pagination-bullets, .swiper-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  border-radius: 20px;
  width: auto;
  left: 50%;
  transform: translateX(-50%);
  padding: 2px;
}
.swiper-container-horizontal > .swiper-pagination-bullets-bullet, .swiper-pagination-bullet {
  height: 8px;
  width: 8px;
  background: #e5e5e5;
  transform: none;
  opacity: 1;
  flex-shrink: 0;
  margin: 2px;
}
.swiper-container-horizontal > .swiper-pagination-bullets-bullet:focus, .swiper-pagination-bullet:focus {
  outline: none;
}
.swiper-container-horizontal > .swiper-pagination-bullets-bullet-active, .swiper-pagination-bullet-active {
  background: yellow;
}
.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 2px;
}

