    text-align: center;
    margin: 0 auto 30px;
  }
}
section.first .container .first__content .first__btn {
  display: block;
  padding: 18px 35px;
  gap: 10px;
  background: #62D7C7;
  border-radius: 10px;
  width: max-content;
  margin: 10px 0 65px;
}
@media (min-width: 1700px) {
  section.first .container .first__content .first__btn {
    margin: 10px 0 125px;
  }
}
@media (min-width: 0px) and (max-width: 767.98px) {
  section.first .container .first__content .first__btn {
    text-align: center;
    margin: 10px auto 50px;
  }
}
@media (min-width: 0px) and (max-width: 479.98px) {
  section.first .container .first__content .first__btn {
    text-align: center;
    margin: 10px auto 40px;
  }
}
section.first .container .first__content .first__btn .first__text-btn {
  font-family: "Mulish";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 36px;
  text-transform: uppercase;
  color: #000000;
}
section.first .container .first__content .first__bottom {
  display: flex;
}
@media (min-width: 0px) and (max-width: 767.98px) {
  section.first .container .first__content .first__bottom {
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}
section.first .container .first__content .first__bottom img {
  margin-right: 15px;
}
@media (min-width: 0px) and (max-width: 767.98px) {
  section.first .container .first__content .first__bottom img {
    margin: 0 auto 15px;
  }
}
section.first .container .first__content .first__bottom .first__text {
  font-family: "Mulish";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  color: #BABABA;
}
section.first .first__background {
  position: absolute;
  top: -240px;
  left: -100px;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: -1;
  height: 100%;
  height: calc(100% + 240px);
}
section.first .first__background .first__image {
  width: 100%;
  background: url("../images/first-bg.jpg") 0 0 no-repeat;
  height: 150%;
  background-size: cover;
  width: calc(100% + 200px);
  animation: bg 30s ease-in-out infinite;
}
@keyframes bg {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(-80px, -380px);
  }
  100% {
    transform: translate(0, 0);
  }
}
section.first .first__background .first__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90.29deg, rgba(0, 0, 0, 0.88) 0.24%, rgba(0, 0, 0, 0.85) 31.39%, rgba(0, 0, 0, 0.6761) 60.17%, rgba(0, 0, 0, 0.2268) 98.56%);
}

.marquee {
  display: none;
}

.partner-brand img {
  filter: grayscale(100%);
}

.txt-big,
.list-li,
.btn,
.footer-link {
  font-size: 20px !important;
}

@media (min-width: 0px) and (max-width: 767.98px) {
  .partner-pic {
    margin: 4em 0 -16em;
  }
  .partner-pic img,
.partner-pic picture {
    max-width: 70%;
  }
}