@import "../variables";

.popup {
	display: none;
	background: #1C1C1C;
	border-radius: 10px;
	max-width: 1000px;
	width: calc(100% - 60px);
	height: calc(100vh - 60px);
	max-height: 65em;
	padding: 0;

	@include w($sm) {
		width: 100%;
		height: 100%;
		padding: 50px 10px;
		max-height: none;
	}

	&-container {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		max-width: 744px;
		height: 100%;
		align-items: center;
		align-content: flex-start;
		margin: auto;

		.title {
			text-align: center;
			max-width: 15.23em;
			margin: .9em auto;
		}
	}

	&-title {
		@include center;
		flex-direction: column;
		width: 100%;
		font-family: 'IBM Plex Mono';
		font-style: normal;
		font-weight: 600;
		font-size: 58px;
		line-height: 1.1379;
		letter-spacing: -0.03em;
		text-transform: uppercase;
		color: #FFFFFF;
	}
}


.fancybox-slide--html .fancybox-close-small {
	padding: 0;
	opacity: 1;
	outline: none;
	top: 15px;
	right: 15px;
}

.fancybox-slide--html {
	padding: 0;
}

.popup-container .title {
	margin: 50px auto 20px;
}


.popup#notify, .popup#notifyme {
	padding: 50px;
	max-height: 500px;
	max-width: 900px;

	@media (min-width: 0px) and (max-width: 767.98px) {
		padding: 30px 20px 50px;
	}

	.popup-title {
		@media (min-width: 0px) and (max-width: 767.98px) {

			.title {
				margin: 0 auto 20px;
			}

			margin: 45px auto 20px;
			font-size: 36px;
		}
	}

	.popup__subtitle {
		font-family: 'Mulish';
		font-style: normal;
		font-weight: 600;
		font-size: 20px;
		line-height: 170%;
		text-align: left;
		color: #BABABA;
		margin: 0 auto 60px;

		@media (min-width: 0px) and (max-width: 767.98px) {
			margin: 0 auto 40px;
		}
	}

	.form {
		position: relative;

		.form--notify {
			max-width: 100%;
			width: 100%;
			height: 84px;
			background: #FFFFFF;
			border-radius: 10px;

			@media (min-width: 0px) and (max-width: 767.98px) {
				height: 70px;
			}

			input {
				margin: 0 205px 0 10px;
				font-family: 'Mulish';
				font-style: normal;
				font-weight: 500;
				font-size: 22px;
				line-height: 170%;
				color: #707070;
				height: 70px;

				@media (min-width: 0px) and (max-width: 767.98px) {
					margin: 0 10px;

				}
			}
		}

		.form--notify .btn {
			position: absolute;
			right: 4px;
			font-family: 'Mulish';
			font-style: normal;
			font-weight: 600;
			font-size: 20px;
			height: 75px;
			line-height: 25px;
			text-transform: uppercase;
			color: #000000;

			@media (min-width: 0px) and (max-width: 767.98px) {
				position: relative;
				right: initial;
				margin-top: 30px;
				height: 70px;
				line-height: 32px;
			}
		}


	}

}


.fancybox-slide--current {
	background: rgba(0, 0, 0, 0.578);
	// backdrop-filter: blur(1px);
}