
//Key-frames==========================================================================================================>
@keyframes swing {
    0% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-5px);
    }
    100% {
      transform: translateY(0);
    }
  }
  
  @keyframes granimate {
    0%, 100% {
      background-position: 0 25%
    }
  
    25%, 75% {
      background-position: 50% 50%
    }
  
    50% {
      background-position: 100% 100%
    }
  }
  
