  @keyframes scaleImg {
    0% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }
  
  @keyframes seal {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  
  @keyframes showProj-blr {
    0% {
      clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
    }
    50% {
      clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 40%);
    }
    75% {
      clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 20%);
    }
    100% {
      clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
    }
  }
  
  @keyframes showProj-brl {
    0% {
      clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
    }
    50% {
      clip-path: polygon(0 100%, 100% 100%, 100% 40%, 0 0);
    }
    75% {
      clip-path: polygon(0 100%, 100% 100%, 100% 20%, 0 0);
    }
    100% {
      clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
    }
  }
  
  @keyframes showProj-lr {
    0% {
      clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    }
    50% {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 60%);
    }
    75% {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    }
    100% {
      clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
    }
  }
  
  @keyframes showProj-rl {
    0% {
      clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    }
    50% {
      clip-path: polygon(0 0, 100% 0, 100% 60%, 0 100%);
    }
    75% {
      clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
    }
    100% {
      clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
    }
  }