@import "variables";

body {
	font-family: $ff-text;
	font-weight: 400;
	@include fluid-type(1250px, 1366px, 8px, 10px);
	position: relative;
	background: $black;
	color: $white;
	overflow-x: hidden;
}

main {
	@include minw($md){
	}
	@include w($sm){
	}
}

.container {
	max-width: 1590px;
	width: 100%;
	padding: 0 60px;
	margin: 0 auto;
	position: static;
	@include w($sm) {
		padding: 0 20px;
	}
}

a{
	transition: $anim;
}

//[class^="title-"]+.arrow-button{
//	margin-bottom: 25px;
//}

.title{
	width: 100%;
	&-center{
		text-align: center;
	}
	&-h1{
		@include h1;
	}
	&-h2{
		@include h2;
		margin-bottom: 50px;
		@include w($sm){
			margin-bottom: 15px;
		}
	}
	&-h3{
		@include h3;
	}
	&-h4{
		@include h4;
	}
}

.btn{
	text-align: center;
	display: inline-flex;
	justify-content: center;
	@include txt(600);
	border: 1px solid;
	padding: 1em 2em;
	text-transform: uppercase;
	transition: $anim;
	border-radius: 10px;
	@include w($sm){
		padding: 18px 35px;
		font-size: 18px;
	}
	&-blue{
		background: $blue;
		border-color: $blue;
		color: $black;
		font-weight: 500;
	}
	&-transparent{
		background: transparent;
		border-color: $blue;
		color: $blue;
	}
	@include offHoverOnDevices{
		&-blue{
			&:hover{
				background: $blue-hover;
				color: $black;
			}
			&:active{
				background: $blue-active;
				color: $black;
			}
		}
		&-transparent{
			&:hover{
				background: $blue;
				color: $black;
			}
			&:active{
				background: $blue-hover;
				color: $black;
			}
		}

	}
}


// === row col ===
.row{
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	width: calc(100% + 20px);
	margin: 0 -10px;
	&-2{
		justify-content: space-between;
		padding-top: 5em;
		&:first-child{
			padding-top: 0;
		}
		&:last-child{
			padding-bottom: 0;
		}
		@include w($sm){
			padding-top: 20px;
		}
	}
	@include w($md){
		justify-content: space-between;
		width: 100%;
		margin: 0;
	}
}
.col{
	&-2{
		@include fluid-type(1250px, 1500px, 8px, 10px);
		width: width(2,20px);
		margin: 10px;
		@include w($md){
			width: 100%;
			margin: 20px auto;
			max-width: 100%;
		}
		@include w($sm){
		}
	}
	&-3{
		width: width(3,30px);
		margin: 15px;
		@include w($md){
			width: calc(50% - 10px);
			margin-left: 0;
			margin-right: 0;
		}
		@include w($sm){
			width: 100%;
		}
	}
}

// === swiper ===
.swiper{
	&-container {
		width: 100%;
		height: 100%;
		overflow: hidden;
		position: relative;
	}
	&-button{
		&-next,
		&-prev {
			z-index: 1;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 32px;
			height: 32px;
			outline: none;
			&::before,
			&::after {
				font-family: swiper-icons;
				text-transform: none!important;
				letter-spacing: 0;
				text-transform: none;
				font-variant: initial;
				line-height: 1;
				font-size: 18px;
				font-weight: 700;
			}
			&::before{
				color: $white;
				position: absolute;
				top: calc(50% - 9px);
				font-weight: 400;
				z-index: 1;
			}
			&::after{
				color: $black;
				position: relative;
				z-index: 2;
				transition: $anim;

			}
			&:focus {
				outline: none;
			}
			@include offHoverOnDevices {
				&:hover {
				}
			}
			&.swiper-button-disabled {
				opacity: 0;
			}
			@include w($sm){
				display: none;
			}

		}
		&-prev {
			&::before{
				content: 'prev';
				left: 9px;
			}
		}
		&-next {
			&::before{
				content: 'next';
				right: 9px;
			}
		}
	}
	&-horizontal>.swiper-pagination-bullets,
	&-pagination-bullets.swiper-pagination-horizontal{
		left: 50%;
		width: auto;
	}
	&-container-horizontal>.swiper-pagination-bullets,
	&-pagination {
		@include center;
		background: $white;
		border-radius: 20px;
		width: auto;
		left: 50%;
		transform: translateX(-50%);
		padding: 2px;
		//width: 100%;
		&-bullet {
			height: 8px;
			width: 8px;
			background: #e5e5e5;
			transform: none;
			opacity: 1;
			flex-shrink: 0;
			margin: 2px;
			&:focus {
				outline: none;
			}
		}
		&-bullet-active {
			background: yellow;
		}
	}
	&-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet{
		margin: 2px;
	}
}

// === text block ===
b{
	font-weight: 700;
	color: $white;
}
.txt{
	@include txt;
	margin-bottom: 10px;
	width: 100%;
}
.txt-big{
	@include txt-big;
	width: 100%;
}
.list{
	list-style: none;
	margin: 25px 0;
	&-li{
		@include txt(400);
		margin-bottom: 20px;
		position: relative;
		padding-left: 32px;
		@include w($sm){
			padding-left: 24px;
			margin-bottom: 10px;
		}
		&:last-child{
			margin-bottom: 0;
		}
		&::before{
			content: '';
			width: 8px;
			height: 8px;
			border-radius: 50%;
			background: $blue;
			display: block;
			position: absolute;
			top: 0.5em;
			left: 0;
			flex-shrink: 0;
		}
	}

}

// === breadcrumbs-section ===
.breadcrumbs {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	width: 100%;
	white-space: nowrap;
	padding: 20px 0;
	@include txt;
	a {
		position: relative;
		display: inline-flex;
		align-items: center;
		color: $black;
		@include offHoverOnDevices{
			&:hover{
			}
		}
	}
	span{
		color: #797979;
	}
	&-separator{
		margin: 0 4px;
		@include center;
		color: $black;
	}
}

// === accordion ===
.accordion{
	display: flex;
	flex-direction: column;
	width: 100%;
	&-head{
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #484848;
		transition: $anim;
		padding: 24px 30px 24px 30px;
		@include h3;
		cursor: pointer;
		color: $white;
		position: relative;
		@include w($sm){
			padding: 16px 30px 16px 0;
		}
		&::before,
		&::after {
			content: '';
			position: absolute;
			right: 0;
			top: 50%;
			background: $blue;
			display: block;
			height: 2px;
			width: 18px;
			transition: $anim;
			border-radius: 2px;
		}
		&::after{
			transform: rotate(90deg);
		}
		.active &::after{
			transform: rotate(180deg);
		}
	}
	&-body{
		display: none;
		overflow: hidden;
		padding: 30px 30px 0;
		width: 100%;

		@include w($md){
			width: 100%;
			padding: 20px 0 0;
		}
	}
}

// === form ===

input,
textarea {
	resize: none;
	@include fixInputIOS;
	&::placeholder{
		color: $grey;
	}
}

input[type=number]{
	-moz-appearance: textfield;
	&::-webkit-inner-spin-button{
		-webkit-appearance: none;
		margin: 0;
	}
	&::-webkit-outer-spin-button{
		-webkit-appearance: none;
		margin: 0;
	}
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
	-webkit-text-fill-color: $black;
	-webkit-box-shadow: 0 0 0 1000px $white inset;
	transition: background-color 5000s ease-in-out 0s;
}

.form {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	@include fluid-type(1300px, 1550px, 8px, 10px);
	label.error {
		position: absolute;
		font-weight: 400;
		font-size: 12px;
		line-height: 16px;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translateY(100%);
	}
	.btn{
		margin: 32px 0 0 auto;
		width: 9.6em;
		text-align: center;
		padding: 1.22em 0;
		@include w($sm){
			margin: 5px 0;
			width: 100%;
			padding: 18px 0;
		}
	}
	&--notify{
		@include center;
		width: 100%;
		max-width: 533px;
		@include txt;
		margin: 0 auto;
		input{
			width: calc(100% - 10em);
			transition: $anim;
			padding: 1.276em;
			@include txt;
			background: $white;
			border-radius: 0;
			color: $black;
		}
		.btn{
			width: 10em;
			margin: 0;
		}
		@include w($sm){
			flex-wrap: wrap;
			input{
				width: 100%;
				padding: 20px 24px;
				margin-bottom: 10px;
			}
			.btn{
				width: 100%;

			}
		}
	}
	&-field {
		display: flex;
		flex-direction: column;
		position: relative;
		width: 100%;
		margin: 12px 0;
		@include w($sm){
			margin: 5px 0;
		}
		input,
		textarea {
			width: 100%;
			transition: $anim;
			padding: 1.276em;
			@include txt;
			background: $white;
			color: $black;
			border-radius: 0;
			&:focus{
			}
			@include w($sm){
				padding: 20px 24px;
			}
		}
		&--half{
			width: calc(50% - 12px);
			@include w($sm){
				width: 100%;
			}
		}
		&--required{
			.form-label::after{
				content: '*';
				color: red;
			}
		}
		&--error{
			input,
			textarea{
			}
		}
	}

}

.hidden {
	display: none;
}
