@import "variables";

.soon{
  background: linear-gradient(0deg, #121212, #202020 20%, #121212 70%);
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  &-content{
    display: flex;
    width: 100%;
    align-items: flex-start;
    justify-content: space-between;
    margin: 7vh 0 20vh;
    @include h(750px){
      margin-top: 3.5vh;
    }
    @include w($sm){
      flex-wrap: wrap;
      margin: 60px 0;
    }
  }
  &-text{
    width: calc(100% -  18.75vw);
    //width: calc(100% - 32.9em);
    //padding-right: 5.5vw;
    .subtitle{
      @include fluid-type(1200px, 1500px, 16px, 20px);
      line-height: 1.8;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      font-weight: 300;
      color: $grey;
      font-family: $ff-text;
      margin-bottom: 4.5vh;
      @include h(620px){
        margin-bottom: 3vh;
      }
    }
    .title{
      margin-bottom: 5.4vh;
      margin-top: 10vh;
    }
    p{
      font-weight: 300;
      line-height: 170%;
      @include fluid-type(1200px, 1366px, 18px, 24px);
      max-width: 27.5em;
      color: $grey;
      margin-bottom: 7.45vh;
    }
    @include w($sm){
      width: 100%;
      padding-right: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      .subtitle{
        margin-bottom: 22px;
        width: 100%;
        @include fluid-type(320px, 450px, 13px, 16px);
      }
      .title{
        margin-bottom: 15px;
        margin-top: 0;
        text-align: center;
      }
      p{
        @include fluid-type(320px, 375px, 17px, 20px);
        text-align: center;
        margin-bottom: 30px;
      }
      .btn{
        margin: 0 auto;
      }
    }
  }
  &-image{
    width:  18.75vw;
    height: 60vh;
    //width: 32.9em;
    @include img(contain);
    picture{
      width: 100%;
      height: 100%;
    }
    @include w($sm){
      height: auto;
      max-width: 300px;
      width: 50vw;
      margin: 60px auto 0;
    }

  }
  &-planet{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 30vh;
    overflow: hidden;
    left: 0;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    @include w($sm){
      height: 55vw;
      max-height: 300px;
      bottom: 120px;
      overflow: visible;
      &::before{
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: 100%;
        height: 120px;
        width: 100%;
        background: linear-gradient(180deg, rgba(18, 18, 18, 0) 0%, #121212 50%);
      }
    }
  }
  &-footer{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 7vh 0;
    color: $grey;
    .container{
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }
    @include h(620px){
      padding: 2.5vh 0;
    }
    svg{
      flex-shrink: 0;
    }
    p{
      @include fluid-type(1200px, 1366px, 17px, 22px);
      color: $grey;
      font-weight: 400;
      padding-left: 15px;
    }
    @include w($sm){
      padding: 16px 0 0;
      align-items: flex-start;
      position: static;
      .container{
        flex-direction: column;
      }
      .icon-lock{
        font-size: 16px;
      }
      p{
        text-align: center;
        padding: 10px 0 0;
      }
    }
  }
  #rotatingGlobe{
    display:block;
    margin-right:auto;
    //margin-left: -10%;
    //max-width: 100%;
    //max-height: 100%;
    //width: 120%;
    //height: 120%;
    @include minw($md){
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
    }
    @include w($md){
      position: static;
      margin-left: -10%;
      width: 120%;
      height: 120%;
    }
    @include w($sm){
      margin-left: -50%;
      width: 200%;
      height: 200%;
    }
  }
}