@import "variables";

.home {}

.counter {
	padding: 13em 0;

	@include w($md) {
		padding: 0;
	}

	@include w($sm) {}

	.container {
		@media (min-width: 0px) and (max-width: 1199.98px) {
			padding-top: 40px;
			padding-bottom: 40px;
		}
	}

	&-content {
		width: 100%;
		@include center;
		flex-wrap: wrap;
	}

	.time-date {
		@include fluid-type(1200px, 1366px, 20px, 26px);
		line-height: 1.7;
		color: $white;
		font-weight: 700;
	}

	.time-countdown {
		@include center;

		&::before {
			content: "|";
			margin: 0 10px 0 20px;

			@include w($sm) {
				content: none;
			}
		}

		.counter--day,
		.counter--hour {
			&::after {
				content: ',';
				padding-left: 3px;
			}
		}

		p {
			margin-left: 10px;
			@include fluid-type(1200px, 1366px, 20px, 26px);
			line-height: 1.7;
			color: $white;
			font-weight: 700;
			display: flex;
			align-items: baseline;

			span {
				//font-weight: 600;
				//@include fluid-type(1250px, 1366px, 34px, 54px);
				//line-height: 122%;
				//color: $white;
				//font-family: $ff;
				padding-right: 6px;
			}
		}

		@include w($sm) {
			padding-top: 22px;
			justify-content: space-between;

			p {
				&:first-child {
					margin-left: 0;
				}

				//flex-wrap: wrap;
				//justify-content: center;
				//font-size: 14px;
				//text-transform: uppercase;
				//margin-left: 0;
				//span{
				//  width: 100%;
				//  text-align: center;
				//  padding: 0 0 10px;
				//  @include fluid-type(320px, 430px, 36px, 40px);
				//}
			}
		}
	}

	&-text {
		font-weight: 400;
		@include h2;
		width: 100%;
		text-align: center;
		margin-bottom: 20px;

		@include w($md) {
			@include fluid-type(320px, 450px, 23px, 30px);
		}
	}
}

@keyframes moveLeft {
	0% {
		transform: translateX(0);
	}

	100% {
		transform: translateX(-50%);
	}
}

.marquee {
	height: 36em;
	width: 100%;
	overflow: hidden;
	box-sizing: content-box;
	position: relative;

	@include w($md) {
		margin: 100px 0;
	}

	@include w($sm) {
		margin: 60px 0;
		max-height: 200px;
		height: 42.667vw;
	}

	&-inner {
		display: flex;
		align-items: center;
		position: absolute;
		will-change: transform;
	}

	&-wrapper {
		display: flex;
		align-items: center;
		animation-name: moveLeft;
		animation-duration: 15s;
		animation-timing-function: linear;
		animation-iteration-count: infinite;
	}

	&-item {
		//display: inline-flex;
		//align-items: center;
		height: 36em;
		width: 36em;
		margin: 0 13px;
		@include img(cover);

		// ------------media-------------
		@include w($sm) {
			max-width: 200px;
			max-height: 200px;
			height: 42.667vw;
			width: 42.667vw;
			margin: 0 5px;
		}
	}
}

.works {
	margin: 18em 0;

	@include w($md) {
		margin: 100px 0 0;

		.row {
			flex-wrap: wrap-reverse;
		}
	}

	@include w($sm) {
		margin: 60px 0 0;
	}

	&-pic {
		display: flex;
		justify-content: right;

		img {
			max-width: none;

			@include w($sm) {
				max-width: 100%;
			}
		}
	}

	&-desc {
		.txt-big {
			margin: 50px 0;

			@include w($sm) {
				margin: 0 0 30px;
			}
		}

	}

	&-list {
		&-item {
			margin-bottom: 50px;
			position: relative;
			padding-left: 56px;

			&:last-child {
				margin-bottom: 0;
			}

			&::before {
				content: '';
				background: url('./../images/bullet.svg') no-repeat center / contain;
				width: 32px;
				height: 32px;
				margin-right: 24px;
				flex-shrink: 0;
				position: absolute;
				top: 0;
				left: 0;
			}

			@include w($sm) {
				margin-bottom: 20px;
				padding-left: 44px;

				&::before {
					width: 24px;
					height: 24px;
					margin-right: 20px;
				}
			}
		}

		.list-li {
			@include txt-big;
		}
	}
}

.partner {
	padding-bottom: 12em;

	@include w($md) {}

	.container {
		background: #1c1c1c;
		border-radius: 10px;
	}

	&-content {
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 7em 0;

		@include w($sm) {
			padding: 40px 0;
		}

		.title {
			text-align: center;
			max-width: 915px;
		}

		.txt-big {
			text-align: center;
			max-width: 915px;
			margin-bottom: 50px;

			@include w($sm) {
				margin-bottom: 15px;
			}
		}
	}

	&-brands {
		width: calc(100% + 120px);
		margin: 0 -60px;
		@include center;
		background: #222222;
		padding: 20px 0;

		@include w($md) {
			flex-wrap: wrap;
			padding: 10px 0;
		}

		@include w($sm) {
			width: calc(100% + 40px);
			margin: 15px -20px 0;
		}
	}

	&-brand {
		margin: 0 4em;
		height: 75px;
		max-height: 75px;
		@include img-full;

		@include w($md) {
			margin: 15px;
			height: 56px;
			max-height: 56px;
		}
	}

	&-pic {
		@include img-full;
		margin: 8em 0 -20em;
	}

}

.earn {
	margin: 18em 0;

	@include w($md) {
		margin: 100px 0 0;
	}

	@include w($sm) {
		margin: 80px 0 0;
	}

	&-pic {
		display: flex;
		justify-content: right;

		img {
			max-width: none;

			@include w($sm) {
				max-width: 100%;
			}
		}
	}

	&-text {
		padding-right: 6em;

		.txt {
			margin-bottom: 50px;
		}

		.title-h3 {
			margin: 50px 0 20px;
		}

		@include w($sm) {
			padding-right: 0;

			.txt {
				margin-bottom: 30px;
			}

			.title-h3 {
				margin: 30px 0 10px;
			}
		}
	}

}

.team {
	@include w($sm) {
		margin: 0 0 80px;
	}

	.container {
		border-radius: 10px;
		background: linear-gradient(120deg, #1c1c1c, transparent 50%);
	}

	&-col {
		width: 40%;
	}

	&-text {
		padding: 7em;

		.txt,
		.txt-big {
			margin-bottom: 50px;
		}

		@include w($sm) {
			max-width: none;

			.txt-big {
				margin-bottom: 30px;
				padding: 0;
			}

			.txt {
				margin: 10px 0 0;
				text-align: left;
			}
		}
	}

	&-pic {
		position: absolute;
		margin-top: -12em;
	}
}

.faq {
	margin: 18em 0;

	@include w($md) {
		margin: 100px 0;
	}

	@include w($sm) {
		margin: 80px 0;
	}

	.title-h2 {
		text-align: center;
	}

	&-more {
		width: 100%;
		@include center;
		margin: 60px auto 0;

		@include w($sm) {
			margin: 30px auto 0;
		}
	}

	&-other {
		display: none;
	}

	&-other.show {
		display: block;
	}

	&-wrap {
		display: flex;
		flex-direction: column;

		button {
			align-self: center;
			margin: 2em;
		}
	}
}

.ceo {
	margin: 18em 0;

	@include w($md) {
		margin: 100px 0;
	}

	@include w($sm) {
		margin: 80px 0;
	}

	.row {
		align-items: stretch;
	}

	&-pic {
		background: #1c1c1c;
		border-radius: 10px;
		height: 100%;
		@include img-full;
		align-items: flex-end;
		align-content: flex-end;
		width: calc(100% - 12em);

		@include w($md) {
			max-width: 400px;
			max-height: 400px;
			width: calc(100vw - 40px);
			height: calc(100vw - 40px);
			padding-top: 60px;
			margin: 0 auto;
		}

		@include w(400px) {
			padding-top: 16vw;
		}
	}

	&-text {
		*:last-child {
			margin-bottom: 0;
		}

		p {
			@include fluid-type(1200px, 1366px, 20px, 24px);
			color: $white;
			margin-bottom: 30px;

			@include w($md) {
				margin-bottom: 10px;
				@include fluid-type(320px, 430px, 20px, 24px);
			}
		}

		span {
			@include txt;
			margin-bottom: 1em;
			display: block;
		}
	}
}

.security {
	background: #1c1c1c;

	&-content {
		max-width: 91.5em;
		width: 100%;
		margin: 0 auto;
		padding: 7em 0;

		@include w($sm) {
			padding: 40px 0;
		}

		.txt-big {
			font-weight: 500;
		}

		.title,
		.txt-big {
			text-align: center;
		}
	}
}



// fixes 22.09.2022

