	border-radius: 10px;
	padding: 50px 0 80px;

	@media (min-width: 0px) and (max-width: 1199.98px) {
		padding: 0px 0 80px;
	}

	@media (min-width: 0px) and (max-width: 767.98px) {
		background: linear-gradient(0deg, #1C1C1C 23.01%, rgba(28, 28, 28, 0) 75.2%);
		width: 100vw;
		margin-left: -20px;
		margin-right: -20px;
		padding-left: 20px;
		padding-right: 20px;
		padding-bottom: 30px;
	}
}

.ceo-pic {
	background: none;

	@media (min-width: 0px) and (max-width: 767.98px) {
		padding-top: 20px;
	}
}

.ceo-text span,
.txt {
	font-size: 20px;
}

// TEAM section
.team-text {
	padding: 7em 0 6em;
	font-size: 20px;
	max-width: 475px;

	@media (min-width: 0px) and (max-width: 1199.98px) {
		max-width: 600px;
		padding: 50px 0 15px;
	}

	@media (min-width: 0px) and (max-width: 767.98px) {
		max-width: 100%;
		padding: 10px 0 15px 0;
	}
}

.team-col {
	@media (min-width: 0px) and (max-width: 1199.98px) {
		width: 48%;
	}

	@media (min-width: 0px) and (max-width: 767.98px) {
		width: 100%;
	}
}

@media (min-width: 0px) and (max-width: 767.98px) {
	.team .container {
		background: linear-gradient(0deg, #1c1c1c, transparent 50%);
	}

	.team .row {
		flex-direction: column-reverse;
	}

	.team-pic {
		position: relative;
		margin-top: 10px;
	}
}


// REFER TO EARN section

.earn-text .txt:last-child {
	margin-bottom: 0;
}

.earn-pic img {
	max-width: 100%;

	@media (min-width: 0px) and (max-width: 1199.98px) {
		margin: 0 auto;
	}
}


// COUNTER section

section.counter {
	background: #0A0A0A;
	padding: 100px 0;

	@media (min-width: 0px) and (max-width: 1199.98px) {
		padding: 40px 0;
	}

	@media (min-width: 0px) and (max-width: 767.98px) {
		padding: 20px 0;
	}

	.container {
		@media (min-width: 0px) and (max-width: 479.98px) {
			padding: 35px 10px;
		}
	}
}

.counter-content {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: baseline;
	margin: auto;
	padding: 28px 30px 30px 30px;
	gap: 30px;
	max-width: 1100px;
	background: #181818;
	border-radius: 10px;

	@media (min-width: 0px) and (max-width: 1199.98px) {
		padding: 15px 15px 17px 15px;
		gap: 20px;
	}

	@media (min-width: 0px) and (max-width: 767.98px) {
		gap: 15px;
	}

	@media (min-width: 0px) and (max-width: 479.98px) {
		gap: 8px;
		padding: 13px 0 15px;
		background: #0A0A0A;
	}

	h2.counter__title {
		font-family: 'IBM Plex Mono';
		font-style: normal;
		font-weight: 400;
		font-size: 46px;
		line-height: 1.4347;
		letter-spacing: -0.03em;
		text-transform: uppercase;
		color: #FFFFFF;

		@media (min-width: 0px) and (max-width: 1199.98px) {
			flex: 0 0 100%;
			text-align: center;
			font-size: 32px;
		}

		@media (min-width: 0px) and (max-width: 767.98px) {
			font-size: 26px;
		}

		@media (min-width: 0px) and (max-width: 479.98px) {

			font-size: 24px;
		}

	}

	span.counter__number {
		font-family: 'IBM Plex Mono';
		font-style: normal;
		font-weight: 600;
		font-size: 54px;
		line-height: 1.2222;
		color: #FFFFFF;
		margin-right: 5px;

		@media (min-width: 0px) and (max-width: 1199.98px) {
			font-size: 38px;
		}

		@media (min-width: 0px) and (max-width: 767.98px) {
			font-size: 26px;
		}

		@media (min-width: 0px) and (max-width: 479.98px) {
			font-size: 17px;
			margin-right: 4px;
		}
	}

	.counter__item {
		font-family: 'Mulish';
		font-style: normal;
		font-weight: 400;
		font-size: 22px;
		line-height: 1.63;
		text-transform: uppercase;
		color: #BABABA;

		@media (min-width: 0px) and (max-width: 1199.98px) {
			font-size: 20px;
		}

		@media (min-width: 0px) and (max-width: 767.98px) {
			font-size: 18px;
		}

		@media (min-width: 0px) and (max-width: 479.98px) {
			font-size: 16px;
		}

	}

	.form--notify input {
		font-size: 20px;
		width: 100%;
		margin: 0 205px 0 10px;
	}

	.popup-container {
		height: auto;
		max-height: 500px;
		padding: 30px;
	}


}


.soon {
	height: auto !important;
	display: none;
}


// FIRST section

section.first {
	position: relative;
	padding: 30px 0 40px;
	width: 100vw;

	@media (min-width: 1700px) {
		padding: 60px 0 40px;
	}

	@media (min-width: 0px) and (max-width: 767.98px) {
		padding: 30px 0 40px;
	}

	.container {
		z-index: 2;

		.first__content {

			.first__about {
				font-family: 'Mulish';
				font-style: normal;
				font-weight: 300;
				font-size: 20px;
				line-height: 36px;
				letter-spacing: 0.1em;
				color: #BABABA;
				margin-bottom: 20px;

				@media (min-width: 0px) and (max-width: 767.98px) {
					text-align: center;
				}

				@media (min-width: 0px) and (max-width: 479.98px) {
					line-height: 32px;
				}
			}

			.first__title {
				font-family: 'IBM Plex Mono';
				font-style: normal;
				font-weight: 600;
				font-size: 65px;
				line-height: 120%;
				letter-spacing: -0.03em;
				text-transform: uppercase;
				color: #FFFFFF;
				margin-bottom: 20px;
				width: 100%;
				max-width: 650px;

				@media (min-width: 1700px) {
					font-size: 82px;
				}

				@media (min-width: 0px) and (max-width: 767.98px) {
					text-align: center;
					font-size: 38px;
				}

				@media (min-width: 0px) and (max-width: 479.98px) {
					font-size: 36px;
					line-height: 115%;
				}
			}

			.first__description {
				font-family: 'Mulish';
				font-style: normal;
				font-weight: 300;
				font-size: 20px;
				line-height: 130%;
				color: #BABABA;
				margin-bottom: 30px;
				max-width: 435px;

				@media (min-width: 0px) and (max-width: 767.98px) {
					text-align: center;
					margin: 0 auto 30px;
				}

				@media (min-width: 0px) and (max-width: 479.98px) {}
			}

			.first__btn {
				display: block;
				padding: 18px 35px;
				gap: 10px;
				background: #62D7C7;
				border-radius: 10px;
				width: max-content;
				margin: 10px 0 65px;

				@media (min-width: 1700px) {
					margin: 10px 0 125px;
				}

				@media (min-width: 0px) and (max-width: 767.98px) {
					text-align: center;
					margin: 10px auto 50px;
				}

				@media (min-width: 0px) and (max-width: 479.98px) {
					text-align: center;
					margin: 10px auto 40px;
				}

				.first__text-btn {
					font-family: 'Mulish';
					font-style: normal;
					font-weight: 600;
					font-size: 20px;
					line-height: 36px;
					text-transform: uppercase;
					color: #000000;
				}
			}

			.first__bottom {
				display: flex;

				@media (min-width: 0px) and (max-width: 767.98px) {
					text-align: center;
					justify-content: center;
					align-items: center;
					flex-direction: column;
				}

				img {
					margin-right: 15px;

					@media (min-width: 0px) and (max-width: 767.98px) {
						margin: 0 auto 15px;
					}
				}

				.first__text {
					font-family: 'Mulish';
					font-style: normal;
					font-weight: 400;
					font-size: 20px;
					line-height: 26px;
					color: #BABABA;
				}
			}
		}
	}

	.first__background {
		position: absolute;
		top: -240px;
		left: -100px;
		right: 0;
		bottom: 0;
		overflow: hidden;
		z-index: -1;
		height: 100%;
		height: calc(100% + 240px);

		.first__image {
			width: 100%;
			background: url('../images/first-bg.jpg') 0 0 no-repeat;
			height: 150%;
			background-size: cover;
			width: calc(100% + 200px);
			animation: bg 30s ease-in-out infinite;
		}

		@keyframes bg {
			0% {
				transform: translate(0, 0);
			}

			50% {
				transform: translate(-80px, -380px);
			}

			100% {
				transform: translate(0, 0);
			}
		}

		.first__cover {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(90.29deg, rgba(0, 0, 0, 0.88) .24%, rgba(0, 0, 0, .85) 31.39%, rgba(0, 0, 0, .6761) 60.17%, rgba(0, 0, 0, .2268) 98.56%);
		}

	}
}

.marquee {
	display: none;
}

.partner-brand img {
	filter: grayscale(100%);
}

.txt-big,
.list-li,
.btn,
.footer-link {
	font-size: 20px !important;
}

@media (min-width: 0px) and (max-width: 767.98px) {
	.partner-pic {
		margin: 4em 0 -16em;
	}

	.partner-pic img,
	.partner-pic picture {
		max-width: 70%;
	}
}