@import "../variables";

.footer {
	margin: 8em 0;

	@include w($md) {
		margin: 50px 0;
	}

	&-content {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
	}

	&-rights {
		@include txt;
		width: 50%;
		text-align: left;


	}

	&-links {
		@include center;
		flex-wrap: wrap;

		@include w($md) {
			width: 100%;
		}
	}

	&-link {
		@include txt;
		color: $grey;
		text-decoration: underline;

		@include offHoverOnDevices {
			&:hover {
				color: $white;
			}
		}

		@include w($sm) {
			width: 100%;
			text-align: center;
			margin: 20px 0;
		}

	}

	&-social {
		@include center;
		margin-left: 24px;
		color: $blue;

		@include offHoverOnDevices {
			&:hover {
				color: $blue-hover;
			}
		}

		&:active {
			color: $blue-active;
		}

		@include w($sm) {
			margin: 0 12px;
		}

	}
}

.footer-rights__separator {
	color: #7B7B7B;
	margin: 0 5px;
}

.footer-link {
	margin-left: 25px;
	margin-right: 25px;
}

@media (min-width: 0px) and (max-width: 1199.98px) {
	.footer-rights {
		text-align: center;
		margin-bottom: 20px;
		flex: 1 1 100%;
	}
}

@media (min-width: 0px) and (max-width: 767.98px) {
	.footer-social {
		margin: 20px 12px 0;
	}

	.footer .container {
		padding: 0 10px;
	}
}