@import "../variables";

.header {
	background: linear-gradient(180deg,#000 13.62%,rgba(0,0,0,.6) 49.17%,rgba(0,0,0,0) 99%);

	&-container {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 4.6vh 0;
		position: relative;

		@include h(620px) {
			padding: 2.5vh 0;
		}

		@include w($sm) {
			padding: 26px 0;
		}
	}

	&-burger {
		display: none;
		position: relative;
		height: 16px;
		width: 24px;

		span {
			width: 100%;
			height: 2px;
			background: $white;
			position: absolute;
			border-radius: 10px;
			display: block;
			left: 0;
			transition: .2s;

			&:nth-child(1) {
				top: 0;
			}

			&:nth-child(2) {
				top: 7px;
			}

			&:nth-child(3) {
				top: 14px;
			}
		}

		.opened & {
			span {
				&:nth-child(1) {
					transform: rotate(45deg);
					top: 4px;
				}

				&:nth-child(2) {
					display: none;
				}

				&:nth-child(3) {
					transform: rotate(-45deg);
					top: 4px;
				}
			}
		}

		@include w($md) {
			display: block;
		}
	}

	&-logo {
		@include img-full;
		width: 21.5em;

		@include w($sm) {
			max-width: 160px;
			width: 43.75vw;
		}
	}

	&-nav {
		display: flex;
		align-items: center;

		@include w($md) {
			display: none;
			position: absolute;
			width: 100%;
			flex-direction: column;
			background: $black;
			left: 0;
			top: 100%;
			z-index: 5;
			height: calc(100vh - 21px - 4.6vh - 4.6vh);

			.opened & {
				display: flex;
			}
		}

		@include h(620px) {
			height: calc(100vh - 21px - 2.5vh - 2.5vh);
		}

		@include w($sm) {
			height: calc(100vh - 21px - 26px - 26px);
			left: -20px;
			width: calc(100% + 40px);
		}
	}

	&-menu {
		@include center;

		&-link {
			color: $white;
			@include fluid-type(1200px, 1366px, 18px, 22px);
			font-weight: 500;
			line-height: 150%;
			margin-right: 3em;

			&:first-child {
				//text-transform: uppercase;
			}

			@include offHoverOnDevices {
				&:hover {
					color: $blue;
				}
			}
		}

		@include w($md) {
			flex-direction: column;
			padding-top: 80px;

			&-link {
				margin: 0 0 60px;
				text-align: center;
				font-size: 20px;
			}
		}
	}
}